* {
  box-sizing: border-box;
  color: #3f3e41;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

header {
  z-index: 100;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 12%;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.fa-arrow-left {
  padding-right: 1rem;
  display: flex;
}

.navigation {
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  display: flex;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

nav a {
  border-bottom: 2px solid #0000;
  padding: .5rem 0;
  font-weight: 550;
  transition: all .3s;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  gap: 1rem;
  font-weight: 550;
  display: flex;
}

.signout-btn, .get-help-btn {
  background-color: #0000;
  border-radius: .5rem;
  padding: .5rem;
  transition: all .3s;
}

.fa-bell {
  transition: all .3s;
}

.fa-bell:hover, section h2 {
  color: #6b2da8;
}

.signout-btn:hover, .get-help-btn:hover {
  background-color: #e2dfe3;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  padding: 7rem 12%;
  display: flex;
}

img {
  width: 500px;
}

.drag-drop-container {
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 50%;
  padding: 1rem;
  transition: all .3s;
  display: flex;
  box-shadow: 5px 10px 15px 10px #dcdcdccf;
}

.drop-zone {
  border: .2rem dashed #6b2da8;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  height: 300px;
  display: flex;
}

.drop-zone i {
  color: #6b2da8;
  font-size: 5rem;
}

.drop-zone p {
  color: #6b2da8;
  font-weight: 700;
}

.file-upload {
  opacity: 0;
  visibility: hidden;
  background-color: #eceaed;
  border-radius: .5rem;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: .5rem 1rem;
  transition: all .3s;
  display: flex;
  transform: translateY(-30px);
}

.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.remove {
  display: none;
}

.file-description {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.fa-file {
  color: #6b2da8;
  font-size: 2rem;
}

.content {
  flex-direction: column;
  gap: .3rem;
  display: flex;
}

.progress-bar {
  background-color: #fff;
  border-radius: .5rem;
  width: 100%;
  height: .5rem;
}

.progress {
  border-radius: inherit;
  background-color: #6b2da8;
  width: 50%;
  height: 100%;
}

.size {
  font-size: .7rem;
}

.fa-solid {
  color: #6b2da8;
}

.fa-cloud-arrow-up {
  transition: all .3s;
}

.uploading {
  transition: all .3s;
  transform: translateY(-30px);
}

@media (width <= 480px) {
  section {
    height: auto;
  }

  .fa-arrow-left {
    color: #3f3e41;
    display: flex;
  }

  header {
    z-index: 100;
    background-color: #fff;
    border-bottom: .5px solid #afafaf;
    width: 100%;
    padding: 1rem;
  }

  .navigation {
    justify-content: center;
    width: 100%;
    display: flex;
  }

  .navigation nav, .user-register {
    display: none;
  }

  .drag-drop-container {
    width: 100%;
  }
}
